/**
 * Fonts
*/

@font-face {
	font-family: "HUGE Avant Garde Bold";
	src: url("./../fonts/huge_agb_v5-webfont.eot?#iefix") format("embedded-opentype"),
		url("./../fonts/huge_agb_v5-webfont.woff") format("woff"),
		url("./../fonts/huge_agb_v5-webfont.ttf") format("truetype"),
		url("./../fonts/huge_agb_v5-webfont.svg#svgFontName") format("svg");
}

@font-face {
	font-family: "Galaxie";
	font-style: normal;
	font-weight: normal;
	src: url("./../fonts/GalaxieCopernicus-Book.eot?#iefix") format("embedded-opentype"),
		url("./../fonts/GalaxieCopernicus-Book.woff") format("woff"),
		url("./../fonts/GalaxieCopernicus-Book.svg#svgFontName") format("svg");
}

@font-face {
	font-family: "Galaxie";
	font-style: italic;
	font-weight: normal;
	src: url("./../fonts/GalaxieCopernicus-BookItalic.eot?#iefix") format("embedded-opentype"),
		url("./../fonts/GalaxieCopernicus-BookItalic.woff") format("woff"),
		url("./../fonts/GalaxieCopernicus-BookItalic.svg#svgFontName") format("svg");
}
