.navigation .nav-list__subnav {
  display: none;
  height: 0;
  overflow: hidden;
  transition: height 350ms ease-in-out;
}

.navigation .nav-list__item--hassub.is-visible .nav-list__subnav {
  display: block;
  height: auto;
}

