@media (max-width: 767px) {
  .hamburger {
    background: transparent;
    border: 0;
    cursor: pointer;
    display: inline-block;
    height: 20px;
    left: 50%;
    margin: 0 0 0 -12px;
    position: absolute;
    top: 20px;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    width: 24px;
  }
  .hamburger-btn {
    background: transparent;
    border: 0;
    cursor: pointer;
    display: block;
    height: 60px;
    left: -12px;
    margin: 0;
    padding: 0;
    position: relative;
    transition: left 0.3s linear, margin 0.3s linear;
    width: 72px;
    z-index: 9;
  }
  .hamburger-btn:focus {
    outline: none;
  }
  .hamburger-btn:focus .hamburger {
    outline: #eeeeee auto 5px;
  }
  .hamburger span {
    background: #eeeeee;
    border-radius: 0;
    display: block;
    height: 4px;
    left: 0;
    opacity: 1;
    position: absolute;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
    width: 100%;
  }
  .hamburger span:nth-child(1) {
    top: 0;
  }
  .hamburger span:nth-child(2), .hamburger span:nth-child(3) {
    top: 8px;
  }
  .hamburger span:nth-child(4) {
    top: 16px;
  }
  .drawer-open .hamburger-btn {
    left: 100%;
    margin-left: -60px;
  }
  .drawer-open .hamburger span:nth-child(1), .drawer-open .hamburger span:nth-child(4) {
    left: 50%;
    top: 8px;
    width: 0;
  }
  .drawer-open .hamburger span:nth-child(2) {
    transform: rotate(45deg);
  }
  .drawer-open .hamburger span:nth-child(3) {
    transform: rotate(-45deg);
  }
}

@media (min-width: 768px) {
  .hamburger-btn {
    display: none;
  }
}

.navigation {
  background: #ec008c;
  height: 72px;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 2;
}

@media (max-width: 767px) {
  .navigation {
    height: 60px;
  }
}

.navigation .container {
  height: 100%;
}

@media (max-width: 767px) {
  .navigation-branding {
    height: 60px;
    left: -100%;
    position: absolute;
    top: 0;
    transition: left 0.3s linear;
  }
}

@media (min-width: 768px) {
  .navigation-branding {
    display: none;
  }
}

@media (max-width: 767px) {
  .navigation-branding__svg {
    fill: #ffffff;
    height: 100%;
    padding: 20px 24px;
  }
}

@media (max-width: 767px) {
  .navigation nav {
    background-color: #ffffff;
    color: #000000;
    height: calc(100vh -  60px );
    left: 0;
    overflow-y: auto;
    padding-left: 0;
    position: absolute;
    right: 100%;
    top: 60px;
    transition: right 0.3s linear;
  }
}

@media (min-width: 768px) {
  .navigation nav {
    float: left;
  }
}

.navigation .nav-list {
  display: block;
  position: relative;
}

.navigation .nav-list::after {
  clear: both;
  content: "";
  display: table;
}

@media (min-width: 768px) {
  .navigation .nav-list {
    top: 12px;
  }
  @supports (display: flex) {
    .navigation .nav-list {
      display: flex;
      height: 72px;
      top: unset;
    }
  }
}

.navigation .nav-list__item {
  display: block;
  position: relative;
}

@media (min-width: 768px) {
  .navigation .nav-list__item {
    float: left;
  }
}

@media (max-width: 767px) {
  .navigation .nav-list__item--hassub.is-visible > a::after {
    transform: rotate(180deg);
  }
  .navigation .nav-list__item--hassub > a {
    position: relative;
  }
  .navigation .nav-list__item--hassub > a::after {
    background-image: url("../images/svg/chevron.svg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    content: "";
    height: 48px;
    position: absolute;
    right: 14px;
    top: 0;
    transition: transform 0.3s ease-in-out;
    width: 20px;
  }
}

.navigation .nav-list__item a {
  cursor: pointer;
  display: block;
  font-family: "HUGE Avant Garde Bold", sans-serif;
  line-height: 48px;
  padding-left: 24px;
  padding-right: 24px;
}

.navigation .nav-list__item > a {
  font-size: 21px;
  transition: color 0.3s cubic-bezier(0, 0, 0.58, 1), background-color 0.3s cubic-bezier(0, 0, 0.58, 1);
}

@supports (display: flex) {
  .navigation .nav-list__item > a {
    align-items: center;
    display: flex;
    height: 100%;
  }
}

@media (max-width: 767px) {
  .navigation .nav-list__item > a {
    color: #000000;
  }
  .navigation .nav-list__item > a:hover {
    background-color: #eeeeee;
  }
}

@media (min-width: 768px) {
  .navigation .nav-list__item > a {
    color: #ffffff;
  }
  .navigation .nav-list__item > a:hover {
    background-color: #ffffff;
    color: #ec008c;
  }
}

.navigation .nav-list__subnav {
  background: #ffffff;
  left: 0;
  white-space: nowrap;
  z-index: 4;
}

@media (max-width: 767px) {
  .navigation .nav-list__subnav {
    position: relative;
    top: 100%;
  }
}

@media (min-width: 768px) {
  .navigation .nav-list__subnav {
    position: absolute;
    top: 60px;
  }
  @supports (display: flex) {
    .navigation .nav-list__subnav {
      top: 72px;
    }
  }
}

.navigation .nav-list__subnav--item {
  display: block;
}

.navigation .nav-list__subnav--item a {
  color: #000000;
  font-family: "Galaxie";
  font-size: 16px;
  overflow: hidden;
  transition-duration: color 0.3s cubic-bezier(0, 0, 0.58, 1), background-color 0.3s cubic-bezier(0, 0, 0.58, 1);
}

.navigation .nav-list__subnav--item a:hover {
  background: #eeeeee;
  color: #ec008c;
}

@media (max-width: 767px) {
  .drawer-open .navigation-branding {
    left: 0;
  }
  .drawer-open .navigation nav {
    right: 72px;
  }
}

.loader {
  color: #ffffff;
  font: 20vmin/1 "HUGE Avant Garde Bold", sans-serif;
  left: 50%;
  letter-spacing: -0.06em;
  margin: 0;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
}

.loader::after {
  content: "Hello.";
}

.loader-body {
  align-content: center;
  background: #ec008c;
  height: 100vh;
  justify-content: center;
  left: 0;
  position: fixed;
  text-align: center;
  top: 0;
  transition: 0.5s all;
  visibility: visible;
  width: 100%;
  z-index: 2;
}

.loader-body.done {
  opacity: 0;
  visibility: hidden;
}

.site-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  bottom: 0;
  cursor: pointer;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: visibility 0.2s ease, opacity 0.3s ease;
  visibility: hidden;
  z-index: 1;
}

@media (min-width: 768px) {
  .site-overlay.is-visible {
    opacity: 0.7;
    transition-delay: 0s;
    visibility: visible;
  }
}

.drawer-open .site-overlay {
  opacity: 0.7;
  transition-delay: 0s;
  visibility: visible;
}
