.loader {
	color: $color-white;
	font: 20vmin/1 $font-family-heading;
	left: 50%;
	letter-spacing: -0.06em;
	margin: 0;
	position: absolute;
	text-align: center;
	top: 50%;
	transform: translate(-50%, -50%);

	&::after {
		content: "Hello.";
	}

	&-body {
		align-content: center;
		background: $color-brand-primary;
		height: 100vh;
		justify-content: center;
		left: 0;
		position: fixed;
		text-align: center;
		top: 0;
		transition: 0.5s all;
		visibility: visible;
		width: 100%;
		z-index: 2;

		&.done {
			opacity: 0;
			visibility: hidden;
		}
	}
}
